import React, { useState } from "react"
import "./style.css"
import quotes from "./quotes"
import FormularioContacto from "./FormularioContacto"

class BookQuotes extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            bookQuotes: quotes,
            book: "Alicia en el País de las Maravillas",
            author: "Lewis Carroll",
            quote: "Se quien era esta mañana cuando .me levanté, pero creo que he debido cambiar varias veces desde entonces"
        }
        this.handleClick = this.handleClick.bind(this)
    }

    handleClick() {
        const randNum = Math.floor(Math.random() * this.state.bookQuotes.length)
        this.setState(prevState => {
            console.log("HIzo lcick!");
            return {
                book: this.state.bookQuotes[randNum].book,
                author: this.state.bookQuotes[randNum].author,
                quote: this.state.bookQuotes[randNum].quote,
            }
        });

    }

    render() {
        return (
            <div class="wrap">
                <button id="get-another-quote-button"
                    onClick={this.handleClick}>Obtener otra frase
                </button>
                <div class="post">
                    <div id="quote-content" >
                        {this.state.quote}
                    </div>
                    <p class="author">
                        <span id="quote-title-dash">—</span> <span id="quote-title">{this.state.author}</span>
                        <span id="quote-source">Lo lei por ahi en: {this.state.book}</span>
                    </p>
                </div>
            </div>
        )
    }
}

// Convert the class below to a functional component that uses the useState hook to initalize a count vartiable to 0 and display the count on the screen.
// Don't worry about the part where the button changes the count quite yet, that's what you're here to learn about!

export default BookQuotes