import React, { useState, useEffect } from "react"
import { BrowserRouter, NavLink, Route, Switch } from "react-router-dom";
import "./App.css"
import AboutMe from "./BookQuotes/AboutMe";
import BookQuotes from "./BookQuotes/BookQuotes";
import Footer from "./BookQuotes/Footer";
import FormularioContacto from "./BookQuotes/FormularioContacto";
import Header from "./BookQuotes/Header";
import Razones from "./BookQuotes/Razones";

class App extends React.Component {

    // inicializamos nuestro estado inicial
    constructor(props) {
        super(props);
        this.state = {
            form: [],
            alert: false,
            alertData: {}
        };
    }

    render() {
        return (
            <BrowserRouter>
                <div className="header-container">
                    <Header />
                    <div className="nav-container">
                        <NavLink className="navlink" to="/">Home</NavLink>
                        <NavLink className="navlink" to="/about">Sobre Mi</NavLink>
                        <NavLink className="navlink" to="/iniciativa">Conoce mas</NavLink>
                        <NavLink className="navlink" to="/form">Comparti tu frase!</NavLink>
                    </div>
                </div>
                <div className="wrapper">
                    <Switch>
                        <Route exact path="/"><BookQuotes /></Route>
                        <Route path="/about"><AboutMe /></Route>
                        <Route path="/iniciativa"><Razones /></Route>
                        <Route path="/form"><FormularioContacto /></Route>
                    </Switch>
                </div>
                <Footer />
            </BrowserRouter>
        )
    }

}
export default App